@import '../styles/templates-styles/variables/hotspots.scss';

.slick-slider{
  margin:0 auto;
}

.slider {
  margin: 0 auto;
  position: relative;
  height: 100%;

  .slider-item {
    height: 100vh;
    .slider__content {
      position: relative;
      bottom: 75%;
      margin-left: 110px;
      .slider__title {
        font-size: 1em;
        color: #A68E52;
        font-weight: normal;
      }
      
      .slider__text{
        font-size: 68px;
        font-weight: 500;
        color: white;
        max-width:700px;
        width: 700px;
      }
        
      .slider__complex-info-btn{
        width: 196px;

        margin-bottom:20px;
        margin-top:50px;
        
      
        padding:16px;
      
        background: #A68E52;
        border:none;
        border-radius: 3px;
        
        
        text-align: center;
      
        color:white;
        font-size: 12px;
        font-weight: 100;
        text-transform: uppercase;
      
      }
      .slick-panel-custom {
        display: flex;
        .slick-arrow{
          margin-top:10px;
          margin-right: 30px;
          color:transparent;
          background-color: #DDDDDD;
      
          border:none;
      
          height:1px;
          width: 75px;
          &:hover{
            cursor: pointer;
          }
        }
      
        .next:after {
          position: relative;
          content: "";
          top: -10px;
          right: -25px;
          width: 0;
          height: 0;
          display: inline-block;
          border-top: 10px solid transparent;
          border-bottom: 10px solid transparent;
          border-left: 10px solid #DDDDDD;
          border-right: 10px solid transparent;
        }
      
        .prev:before{
          position: relative;
            content: "";
            top: -9px;
            left: -45px;
            width: 0;
            height: 0;
            display: inline-block;
            border-top: 10px solid transparent;
            border-bottom: 10px solid transparent;
            border-right: 10px solid #DDDDDD;
            border-left: 10px solid transparent;
        }
      }
    }
    .slider__image {
      width: 100%;
      height: 100%;

      object-fit: cover;
    }
  }

  .slick-track{
    display: flex;
  }


  .slick-dots{

    margin-top:10px;

    display: flex;
    list-style: none;
    padding: 0;
    li {
      margin-right: 25px;
    button{
      padding:0;
      width: 8px;
      height: 8px;
      border-radius: 50%;
      border:none;
      background: #DDDDDD;
      color:transparent;
      opacity: 0.4;

    }

    }
    li.slick-active{
      button{
        background: #757575;
      }
    }
  }

}

//orientation landscape

@media (max:320px) and (orientation:landscape)  {
  .slider .slider-item .slider__content .slick-panel-custom .prev:before {
    left:-25px;
    top:-10px;
    border-right: 5px solid #DDDDDD;
  }
  .slider .slider-item .slider__content .slick-panel-custom .next:after {
    top:-28px;
    border-left: 5px solid #DDDDDD;
  }
  .slider {
    .slider-item {
      .slider__content {
        margin-left: 20px;
        bottom:80%;
        .slider__title {}
        .slider__text {
          font-size: 18px;
        }
        .slider__complex-info-btn {
          width: 120px;

          margin-top: 20px;
          padding: 10px;

          font-size: 10px;
        }
        .slick-panel-custom {
          .slick-arrow {
            margin-top:0px;
            width: 40px;
            margin-right: 15px;

            height: 1px;
            transform: scaleY(0.5);
          }
          .slick-dots {
            margin-top:0px;
            position: relative;
            margin-left: 35px;
            bottom: 5px;
            li {
              button {
                height:10px;
                width:10px;
                border:0.5px solid rgb(163, 163, 163);
              }
            }
            li.slick-active{
              button{
                background: rgb(138, 120, 123);
                opacity: 0.8;
               
              }
            }
          }


        }
      }
    }
  }
}
